<script>
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import Stat from '@components/widget-stat'
import axios from 'axios';
import Utils from '../../../../../utils/Util'

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Dashboard',
	},
	components: { Layout, PageHeader, Stat },
	data() {
		return {
			api: Utils.Host(),
			title: 'Dashboard',
			jumlahdataall: [
			],
			jumlahdataallstatus: [
			],
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdatajumlahdataall()
    },
	methods: {
      async Getdatajumlahdataall() {
        try {
            let response1 = await axios.get(this.api+"/dashboard/jumlahdataall", Utils.authHeader());
			for (const tampil of response1.data.Data) {
				tampil.title = 'Tahun - ' + tampil.title;
				tampil.icon = 'file-text';
				tampil.color = 'primary';
			}
            this.jumlahdataall = response1.data.Data;
			let response2 = await axios.get(this.api+"/dashboard/jumlahdataallstatus", Utils.authHeader());
			for (const tampil of response2.data.Data) {
				tampil.title = 'Tahun - ' + tampil.title + ' - ' + tampil.status;
				tampil.icon = 'file-text';
				tampil.color = 'danger';
			}
            this.jumlahdataallstatus = response2.data.Data;
        } catch (err) {
            console.error(err);
        }
      },
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">

									<div class="row">
									<div v-for="stat of jumlahdataall" :key="stat.title" class="col-md-6 col-xl-3">
										<Stat
											:title="stat.title"
											:value="stat.value"
											:icon="stat.icon"
											:color="stat.color"
										/>
									</div>
									</div>

									<br>

									<div class="row">
									<div v-for="stat of jumlahdataallstatus" :key="stat.title" class="col-md-6 col-xl-3">
										<Stat
											:title="stat.title"
											:value="stat.value"
											:icon="stat.icon"
											:color="stat.color"
										/>
									</div>
									</div>

									

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
